@media only screen and (max-width: 600px) {
  .main__description--title {
    font-size: 30px; }
  .footer {
    align-items: flex-start;
    flex-direction: column; } }

body {
  margin: 0;
  font-family: "Muli", sans-serif;
  background: #2E4053; }

* {
  box-sizing: border-box; }

.button {
  background-color: rgba(255, 255, 255, 0.3);
  border: none;
  border-radius: 25px;
  color: white;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  font-family: 'Muli', sans-serif;
  height: 50px;
  letter-spacing: 1px;
  margin: 10px 0; }
